<template>
	<div :class="'nav-wrap-'+classId">
		<div :class="'nav-item-wrap-'+classId" v-for="(nav,index) in navList" :key='index'>
			<div :class="['nav-item-'+classId,{active: nav.active!==undefined ? nav.active : actives[index]}]" @click="handleClick(nav)">
				<div :class="'nav-text-'+classId" v-text='nav.navText'></div>
			</div>
			<navigator-content :classId='classId + 1' :navList='nav.children' :isWrap='false' :ends='ends' v-if='nav.children' v-show="nav.depoly" />
		</div>
	</div>
</template>

<script>
	export default {
		name: 'navigatorContent',
		props: {
			classId: {
				type: Number,
				default: 1
			},
			navList: {
				type: Array,
				required: true
			},
			isWrap: {
				type: Boolean,
				default: true
			},
			ends: {
				type: Array,
				default(){
					return []
				}
			}
		},
		mounted(){
			this.isWrap && this.each(this.navList)
		},
		computed: {
			actives(){
				let actives = []
				this.navList.forEach(nav=>{
					actives.push(nav.children && nav.children.find(child=>child.active))
				})
				return actives
			}
		},
		methods: {
			handleClick(nav){
				if(nav.active!==undefined){
					this.ends.forEach(end=>{end.active = end===nav})
				}else{
					nav.depoly = !nav.depoly
				}
				if(nav.to){
					this.$router.push(nav.to)
				}
			},
			each(list){
				list.forEach(li=>{
					if(li.active!==undefined){
						li.active = this.$route.path === li.to
						this.ends.push(li)
					}
					li.children
					&&	this.each(li.children)
				})
			}
		},
		watch: {
			'$route.path': {
				handler(val){
					this.isWrap && this.each(this.navList)
				}
			}
		}
	}
</script>

<style lang="scss" scoped="scoped">
	@import "../../../assets/styles/mixin.scss";
	.nav-wrap-1{
		font-family: "microsoft yahei";
		.nav-item-wrap-1{
			.nav-item-1{
				height: 26px;
				padding: 17px 0;
				background: #fff;
				cursor: pointer;
				.nav-text-1{
					font-size: 18px;
					line-height: 26px;
					color: $color-text;
					padding: 0 30px;
					box-sizing: border-box;
				}
				&:hover{
					background: $color-active-text;
					.nav-text-1{
						color: #fff;
					}
				}
				&.active{
					.nav-text-1{
						border-left: 3px solid $color-active-text;
						padding: 0 27px;
					}
				}
			}
		}
		.nav-wrap-2{
			.nav-item-2{
				height: 14px;
				padding: 13px 0;
				background: #fff;
				cursor: pointer;
				.nav-text-2{
					line-height: 14px;
					font-size: 14px;
					color: $color-text;
					text-align: center;
				}
				&:hover{
					background: $color-active-text;
					.nav-text-2{
						color: #fff;
					}
				}
				&.active{
					.nav-text-2{
						color: $color-active-text;
					}
				}
				&.active:hover{
					.nav-text-2{
						color: #fff;
					}
				}
			}
		}
	}
</style>
